
      
import {LitElement, html} from 'lit-element';
import {transition} from 'lit-transition';

export class Comp extends LitElement {
  // a prop that we toggle and what will trigger redraw
  static get properties() { return { a: Boolean } }

  // swapped template is transitioned automatically
  get swapped() {
    return transition( // <- this is all!
      this.a ? html`<h2>Cool stuff!</h2>` 
             : html`<h2>Click me</h2>`
    );
  }
  
  render() {
    return html`<center @click=${() => this.a = !this.a}>
      ${this.swapped}
    </center>`
  } 
}

      export const code = "\nimport {LitElement, html} from 'lit-element';\nimport {transition} from 'lit-transition';\n\nexport class Comp extends LitElement {\n  // a prop that we toggle and what will trigger redraw\n  static get properties() { return { a: Boolean } }\n\n  // swapped template is transitioned automatically\n  get swapped() {\n    return transition( // <- this is all!\n      this.a ? html`<h2>Cool stuff!</h2>` \n             : html`<h2>Click me</h2>`\n    );\n  }\n  \n  render() {\n    return html`<center @click=${() => this.a = !this.a}>\n      ${this.swapped}\n    </center>`\n  } \n}\n";
    